var sourceType = [
	['camera'],
	['album'],
	['camera', 'album']
]
var sizeType = [
	['compressed'],
	['original'],
	['compressed', 'original']
]

var uploadFlag = false; // 标志文章正在上传，为 true 时 block 该方法
var requestTask;
var uploadTasks = [];
export default {
	data() {
		return {
			userInfo: '',
			articleTitle: '',
			articleContent: '',
			articleTag: '',
			imgPath: '',
			showInputTagArea: 0,
			showAddTagButton: 1,
			showTagArea: 0,
			tagList: [],
			finalTag: '',
			tagIndex: 0,

			imageList: [],
			sourceTypeIndex: 2,
			sourceType: ['拍照', '相册', '拍照或相册'],
			sizeTypeIndex: 0,
			sizeType: ['压缩', '原图', '压缩或原图'],
			countIndex: 8,
			count: [1, 2, 3, 4, 5, 6, 7, 8, 9]
		}
	},
	onUnload() {
		this.imageList = [],
			this.sourceTypeIndex = 2,
			this.sourceType = ['拍照', '相册', '拍照或相册'],
			this.sizeTypeIndex = 0,
			this.sizeType = ['压缩', '原图', '压缩或原图'],
			this.countIndex = 8;
	},
	onLoad() {
		this.userInfo = this.getGlobalUserInfo();
	},
	methods: {
		addTag: function() {
			this.showInputTagArea = 1;
			this.showAddTagButton = 0;
		},
		// 检查tagList的数量
		checkInput: function(res) {
			var that = this;
			var tag = this.articleTag;
			//console.log(tag)
			if (this.isNull(tag)) {
				that.showAddTagButton = 1;
				that.showInputTagArea = 0;
			} else {
				// 显示标签区域 = 1
				that.showTagArea = 1;

				//console.log(that.tagIndex);
				that.tagList[that.tagIndex] = tag;
				that.tagIndex = that.tagIndex + 1;
				that.showAddTagButton = 1;
				that.showInputTagArea = 0;
				that.articleTag = '';
			}

		},

		combineTagToString: function(res) {
			var that = this;
			for (var i = 0; i < that.tagList.length; i++) {
				that.finalTag = that.finalTag + '#' + that.tagList[i];
			}
		},

		sourceTypeChange: function(e) {
			this.sourceTypeIndex = parseInt(e.target.value)
		},
		sizeTypeChange: function(e) {
			this.sizeTypeIndex = e.target.value
		},
		countChange: function(e) {
			this.countIndex = e.target.value;
		},
		chooseImg: async function() {

			uni.chooseImage({
				sourceType: sourceType[this.sourceTypeIndex],
				sizeType: sizeType[this.sizeTypeIndex],
				count: this.imageList.length + this.count[this.countIndex] > 9 ? 9 - this.imageList.length : this.count[this.countIndex],
				success: (res) => {
					this.imageList = this.imageList.concat(res.tempFilePaths);

					console.log(res)
				}
			})
		},
		previewImage: function(e) {
			var current = e.target.dataset.src
			// console.log(e)
			console.log(this.imageList);
			uni.previewImage({
				current: current,
				urls: this.imageList
			})
		},
		isAddImage: function() {
			if (this.imageList.length >= 9) {
				return false;
			} else {
				return true;
			}
		},
		
		// TODO：图片上传需加上大小限制，后台限制10M
		upload: function(e) {
			var me = this;
			if (me.articleTitle == '' || me.articleTitle == null) {
				uni.showToast({
					icon: 'none',
					title: '文章标题不能为空～',
					duration: 1000
				});
				return;
			}

			if (me.articleContent == '' || me.articleContent == null) {
				uni.showToast({
					icon: 'none',
					title: '文章内容不能为空～',
					duration: 1000
				});
				return;
			}

			if (uploadFlag) {
				console.log("正在上传...")
				return;
			}
			uploadFlag = true;
			uni.showLoading({
				title: "正在上传..."
			})
			
			setTimeout(()=>{
				if(uploadFlag){
					uploadFlag = false // 解锁
					uni.hideLoading();
					uni.showToast({
						title: "网络未知错误",
						icon: "none",
						duration: 1000
					})
					// TODO: 终止上传
					for (let task in uploadTasks) {
						console.log(task)
						task.abort();
					};
					requestTask.abort();
				}
			}, 5000); // 延时5s timeout
			
			setTimeout(() => {
				me.combineTagToString();

				var serverUrl = me.$serverUrl;
				requestTask = uni.request({
					url: serverUrl + '/article/uploadArticle',
					method: 'POST',
					data: {
						userId: me.userInfo.id,
						articleTag: me.finalTag,
						articleTitle: me.articleTitle,
						articleContent: me.articleContent
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: (res) => {
						// console.log(res);
						me.finalTag = ""; // 清空组装tag
						if (res.data.status == 200) {
							if (me.imageList.length > 0) {
								const articleId = res.data.data;
								var resCount = 0
								for (var i = 0; i < me.imageList.length; i++) {
									uploadTasks[i] = uni.uploadFile({
										url: this.$serverUrl + '/article/uploadArticleImg',
										filePath: me.imageList[i],
										name: 'file',
										formData: {
											userId: me.userInfo.id,
											articleId: articleId,
											order: i
										},
										success: (uploadFileRes) => {
											resCount++;
											if(resCount == me.imageList.length){
												me.uploadSuccess();
											}
										}
									});
								}
							}else{
								me.uploadSuccess();
							}
						} else if(res.data.status == 501){
							me.contentIllegal();
						} else {
							me.uploadFail();
						}
					},
					fail: (res) => {
						me.uploadFail();
					}
				})
			}, 100) //延时执行等待上锁
		},
		
		uploadSuccess(){
			uploadFlag = false;
			uni.hideLoading();
			uni.$emit("flash"); // 给 index 发送刷新信号
			uni.navigateBack({
				delta: 1
			})
			uni.showToast({
				title: '已提交审核',
				duration: 2000,
				icon: 'success',
			})
		},
		
		uploadFail(){
			// 上传失败 用户提醒
			uploadFlag = false;
			uni.hideLoading();
			uni.showToast({
				title: '出现未知错误，上传失败',
				duration: 2000,
				icon: 'none',
			})
		},
		
		contentIllegal(){
			// 内容非法 用户提醒
			uploadFlag = false;
			uni.hideLoading();
			uni.showToast({
				title: '内容涉嫌违规，请联系管理员。',
				duration: 2000,
				icon: 'none',
			})
		},
		
		deleteTag: function(index) {
			console.log(index);
			var targetTag = this.tagList[index];
			this.tagList.splice(index, 1);
			console.log(this.tagList.length);
			this.tagIndex = this.tagList.length;
		},
		deleteImg: function(index) {
			// console.log(index);
			// console.log(this.imageList[index]);
			this.imageList.splice(index, 1);
		},
	}
};
